import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Customer } from '../customer/signup/signup.component';
import { ShaeredService } from '../shared/shaered.service';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {

  productList: Product[] = [];

  constructor(private router: Router, private route: ActivatedRoute, private sharedService: ShaeredService) { }

  
  ngOnInit() {

    this.route.params.subscribe(params => {
      // PARAMS CHANGED .. TO SOMETHING REALLY COOL HERE ..
 
      // for example extract the id..

      let id = +params['id'];
      if(params['type']== 'model'){
         // (+) converts string 'id' to a number
        this.getProductByModelId(id);
      }
      else if(params['type']== 'subCategory'){
        this.getProductBySubCategoryId(id);
      }   
    });
  }

  getProductByModelId(id: number){
    //let id = +this.route.snapshot.paramMap.get('id');

    //this.loadingService.loading = true;
    console.log("model id", id);
    this.sharedService.getProductByModelId(id)
    .subscribe((product) =>{
      //this.productList = product;
      let selectedCustomer: Customer = this.sharedService.getCustomerDetailsForSale();

      if(selectedCustomer){

          product.forEach((product)=>{
          if(selectedCustomer.tier == 3){
            product.retail = product.tier3;
          }
          else if(selectedCustomer.tier == 2){
            product.retail = product.tier2;
          }
          else if(selectedCustomer.tier == 1){
            product.retail = product.tier1;
          }
        });
      }
      this.productList = product;
      this.productList = this.productList.slice();

    // this.loadingService.loading = false;
    })
  }
  getProductBySubCategoryId(id: number){
    //let id = +this.route.snapshot.paramMap.get('id');

    //this.loadingService.loading = true;
    console.log("model id", id);
    this.sharedService.getProductBySubCategoryId(id)
    .subscribe((product) =>{
      //this.productList = product;
      let selectedCustomer: Customer = this.sharedService.getCustomerDetailsForSale();

      if(selectedCustomer){

          product.forEach((product)=>{
          if(selectedCustomer.tier == 3){
            product.retail = product.tier3;
          }
          else if(selectedCustomer.tier == 2){
            product.retail = product.tier2;
          }
          else if(selectedCustomer.tier == 1){
            product.retail = product.tier1;
          }
        });
      }
      this.productList = product;
      this.productList = this.productList.slice();

    // this.loadingService.loading = false;
    })
  }


}


export class Product {
  transactionLineItemId: number;
  productNo: string;
  description: string;
  categoryId: number;
  brandId: number;
  vendorId: number;
  modelId: number;
  cost: number;
  retail: number;
  tier1:number;
  tier2:number;
  tier3:number;
  quantity: number;
  saleQuantity: number;
  ecommerce: boolean;
  tax:boolean
  image: any;
  customerPhoneNo: string;
  date: any;
  status: any;
  featured:boolean;
  onSale: boolean;
  newProduct:boolean;
}
