import { Component, OnInit } from '@angular/core';
import { ShaeredService } from '../../shared/shaered.service';
import { Product } from '../../product/product.component';
declare var $: any;


@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  newProduct: Product[] = [];

  constructor(private sharedService: ShaeredService) { }

  ngOnInit() {
    this.getNewProducts();
  }
  ngAfterViewInit() {
   // this.test();

  }

  getNewProducts() {


    this.sharedService.getNewProducts()
    .subscribe((newProduct: Product[])=>{
      this.newProduct = newProduct;

       
  
      
    });
  }

  test (){


    $(".product-slider").owlCarousel(
      {
        items : 6,
          loop: true,
          autoplay: true,
          autoplaySpeed: 3000,
          autoplayHoverPause: false,
          responsive:
          {
            0:{
                items:1,
                dots: false,
            },
            600:{
                items:3,
            },
            1000:{
                items:5,
            },
            1400:{
                items:6,
                loop:true
            }
        }
      });
  }

}
