import { Component, OnInit } from '@angular/core';
import { ShaeredService } from '../shaered.service';
import { Router, ActivatedRoute } from '@angular/router';
import * as $ from 'jquery';
import { Product } from '../../product/product.component';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  menuDto: WebMenuDto;
  selectedBrand: WebBrandDto;
  selectedCategory: CategoryDao;

  constructor(private sharedService: ShaeredService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {

    this.getMenuDetail();
  }

  onBrandSelect(brand: WebBrandDto) {
    this.selectedBrand = brand;

    $('.has-mega-menu').on('click', function ()
	  {
		  $('.submenu').fadeIn('200');
	  });
  
	  $('.thumbnail').on('click', function () 
	  {
		  $('.brand-submenu').fadeToggle('slow');
    });
    
	  $('.brand-submenu a, .thumbnail-two').on('click', function () 
	  {
		  $('.submenu').fadeOut('200');
	  });
    console.log('selected Brand', brand);
  }
  onCategorySelete(category: CategoryDao) {

    this.selectedCategory = category;
    
    $('.has-mega-menu').on('mouseover', function ()
	  {
		  $('.submenu').fadeIn('200');
	  });
  
	  $('.thumbnail-two').on('click', function () 
	  {
		  $('.brand-submenu').fadeToggle('slow');
    });
    
	  $('.brand-submenu a, .thumbnail-two').on('click', function () 
	  {
		  $('.submenu').fadeOut('200');
	  });

  }

  getMenuDetail() {

    this.sharedService.getWebMenu()
    .subscribe((response: WebMenuDto) => {
      this.menuDto = response;
      console.log('Webmenu response', this.menuDto);
    });

  }



}

export class WebMenuDto {

  categoryDtoList: CategoryDao[];
  webBrandDtoList: WebBrandDto[];
  modelDtoList: ModelDao[];
}

export class CategoryDao {
  categoryId: number;
  name: string;
  description: string;
  subCategoryDaoList: SubCategoryDao[];
}
export class WebBrandDto {
  brandId: number;
  brandName: string;
  brandImage: any;
  modelDtoList: ModelDao[];
}
export class ModelDao {
  modelId: number;
  name: string;
  description: string;
}
export class SubCategoryDao {
  id: number
  categoryId: number;
  name: string;
  description: string;
}
